.about-body{
	width: 88%;
	height: auto;
	background-repeat: no-repeat;
	background-size: cover;
    margin: auto;
    margin-bottom: 70px;
}
.about-row{
	display: flex;
	flex-wrap: wrap;
	margin-top: 50px;
	align-items: center;
	justify-content: space-between;
	background-color: #007CC7;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
}
.about-col-1{
	padding-left: 10%;
	max-width: 680px;
	color: #f2f2f2;
	letter-spacing: 1px;
	margin-top: none;
	padding-right: 10px;
}
.about-col-1 .about-title{
	color: #c8d8e4;
	font-size: 36px;
	font-weight: 600;
	line-height: 50px;
	margin-bottom: 20px;
	font-family: 'Sansita Swashed', cursive;
}
.about-btn{
	box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
	background-color: #c8d8e4;
  	color: black;
  	font-size: 15px;
  	font-weight: bold;
  	border-radius: 10px;
  	border: 2px solid #084869;
  	padding: 12px 28px;
  	margin-top: 30px;
  	transition-duration: 0.4s;
  	margin-bottom: 15px;
}
.about-btn:hover{
	background-color: #084869;
	color: white;
}
.about-col-2{
	text-align: center;
	position: relative;
	margin-top: 0px;
	background-image: linear-gradient(to bottom right,#007CC7 50%,#fafafa 50%);
}
.about-col-2 .about-image{
	width: 60%;
	max-width: 450px;
	position: relative;
	z-index: 2;
	transition: transform 0.5s;
	margin-top: 25px;
	border-radius: 50%;
}
.about-col-2 .about-back-image{
	position: absolute;
	padding-right: 15px;
	width: 60%;
	max-width: 450px;
	top: -12px;
	right: 12px;
	margin-top: 20px;
	border-radius: 50%;
}
.about-image:hover{
	transform: translate(-25px,-25px);
	cursor: pointer;
}
@media (max-width: 850px) {
	.about-body{
		width: 100%;
	}
}