@import url(https://fonts.googleapis.com/css?family=Raleway:300);
@import url(https://fonts.googleapis.com/css?family=Lusitana:400,700);

.contactus-body {
 width: 88%;
 height: 10%;
  position: relative;
  background-color: #d35f5f;
  margin: auto;
  margin-bottom: 50px;
}

.align-center {
  text-align: center;
}

.row {
  margin: -20px 0;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.row .col {
  padding: 0 20px;
  float: left;
  box-sizing: border-box;
}
.row .col.x-50 {
  width: 50%;
}
.row .col.x-100 {
  width: 100%;
}
.content-wrapper {
  min-height: 100%;
  position: relative;
}

.contactus-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contactus-container .contactus-title {
  text-align: center;
  font-family: Raleway, sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 36px;
  line-height: 48px;
  padding-bottom: 48px;
}

.contact-form .form-field {
  position: relative;
  margin: 32px 0;
}
.contact-form .contactus-input-text {
  display: block;
  width: 100%;
  height: 36px;
  border-width: 0 0 2px 0;
  border-color: #888;
  font-family: Lusitana, serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  background-color: #fffafa;
}
.contact-form .contactus-input-text:focus {
  outline: none;
}
.contact-form .contactus-input-text:focus + .contactus-label,
.contact-form .contactus-input-text.not-empty + .contactus-label {
  transform: translateY(-24px);
}
.contact-form .contactus-label {
  position: absolute;
  left: 20px;
  bottom: 30px;
  font-family: Lusitana, serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  color: #333;
  cursor: text;
  transition: transform 0.2s ease-in-out;
}
.contact-form .contactus-submit-btn {
  display: inline-block;
  background-color: #be4747;
  color: #fff;
  font-family: Raleway, sans-serif;
  letter-spacing: 2px;
  font-size: 16px;
  font-weight: bold;
  line-height: 24px;
  padding: 12px 50px;
  border: none;
  cursor: pointer;
}
@media only screen and (max-width: 800px) {
    .contactus-body {
      width: 100%;
      
    }
  }