
.navbar-body {
  overflow-x: hidden;
}

.navbar{
  width: 100vw;
  background-color: #fff;
  display: flex;
  z-index: 10;
}

/*Styling Links*/
.nav-links {
  display: flex;
  list-style: none;
  width: 100%;
  padding: 2vw 5vw;
  justify-content: space-between;
  text-transform: uppercase;
}
.nav-links ul {
  list-style: none;
  display: flex;
}
.nav-links li a {
  text-decoration: none;
  margin: 0 0.7vw;
  color: #111;
  font-size: 18px;
  padding: 20px;
}
.nav-links .navbar-right li a {
  font-size: 20px;
}
.nav-links li {
  position: relative;
}

/*Styling Hamburger Icon*/
.navbar-hamburger div {
  width: 30px;
  height: 3px;
  background: #111;
  margin: 5px;
  transition: all 0.3s ease;
}
.navbar-hamburger {
  display: none;
  margin-top: 20px;
}

/* Main */
.navbar-container {
  margin: 0px 87px;
}
.navbar-container img {
  width: 100%;
  height: 450px;
}

/* Search Bar */
.navbar-search {
  margin: auto;
  width: 30%;
  margin-top: -30px;
}
.navbar-search-box {
  width: 450px;
  position: relative;
  display: flex;
}
.navbar-search-input {
  width: 100%;
  padding: 10px;
  border: 2px solid #111;
  border-right: none;
  outline: none;
  font-size: 20px;
  color: #111d5e;
  background: #fff;
}
.navbar-search-button {
  text-align: center;
  height: 51px;
  width: 40px;
  outline: none;
  cursor: pointer;
  border: 2px solid #111;
  border-left: none;
  background: none;
  font-size: 17px;
  background-color: #111;
  color: #fff;
}

/*Stying for small screens*/
@media screen and (max-width: 950px) {
  nav {
    position: fixed;
    z-index: 3;
  }
  .navbar-hamburger {
    display: block;
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 50%;
    transform: translate(-5%, -50%);
    z-index: 2;
    transition: all 0.7s ease;
  }
  .nav-links {
    position: fixed;
    background: #131418;
    height: 100vh;
    width: 100%;
    padding: 0;
    flex-direction: column;
    justify-content: none;
    clip-path: circle(50px at 90% -20%);
    -webkit-clip-path: circle(50px at 90% -10%);
    transition: all 1s ease-out;
    pointer-events: none;
  }
  .nav-links ul {
    display: block;
    text-align: center;
    padding: 30px;
  }
  .nav-links ul:first-child {
    margin-bottom: 0;
  }
  .nav-links ul li {
    padding-bottom: 70px;
  }

  .nav-links .navbar-left li:last-child {
    padding-bottom: 0px;
  }
  .nav-links .navbar-right li:last-child {
    padding-bottom: 0px;
  }
  .nav-links li a {
    color: #fff;
  }
  .nav-links.open {
    clip-path: circle(1000px at 90% -10%);
    -webkit-clip-path: circle(1000px at 90% -10%);
    pointer-events: all;
  }
  .nav-links li:nth-child(1) {
    transition: all 0.5s ease 0.2s;
  }
  .nav-links li:nth-child(2) {
    transition: all 0.5s ease 0.4s;
  }
  .nav-links li:nth-child(3) {
    transition: all 0.5s ease 0.6s;
  }
  .nav-links li:nth-child(4) {
    transition: all 0.5s ease 0.7s;
  }
  .nav-links li:nth-child(5) {
    transition: all 0.5s ease 0.8s;
  }
  .nav-links li:nth-child(6) {
    transition: all 0.5s ease 0.9s;
    margin: 0;
  }
  .nav-links li:nth-child(7) {
    transition: all 0.5s ease 1s;
    margin: 0;
  }
  .navbar-container {
    margin: 0px;
  }
  .navbar-container img {
    margin-top: 50px;
    height: auto;
  }
  .navbar-search {
    width: auto;
    margin: 0 50px;
    margin-top: -30px;
  }
  .navbar-search-box {
    width: auto;
  }
}

/*Animating Hamburger Icon on Click*/
.toggle .navbar-line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
  background: #fff;
}
.toggle .navbar-line2 {
  transition: all 0.7s ease;
  width: 0;
}
.toggle .navbar-line3 {
  transform: rotate(45deg) translate(-5px, -6px);
  background: #fff;
}
