:root {
  --surface-color: #fff;
  --curve: 30;
}

* {
  box-sizing: border-box;
}

.latestupdates-body {
  width: 88%;
  background-color: #EEFBFB;
  margin: auto;
}
.latestupdates-h1{
  text-align: center;
}

.latestupdates-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin: 4rem 5vw;
  padding: 0;
  list-style-type: none;
  
}

.latestupdates-card {
  position: relative;
  display: block;
  height: 100%;  
  overflow: hidden;
  text-decoration: none;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
  margin-bottom: 50px;
}

.latestupdates-card__image {      
  width: 100%;
  height: auto;
}

.latestupdates-card__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;      
  border-radius: calc(var(--curve) * 1px) 0px 0px 0px;    
  background-color: var(--surface-color);      
  transform: translateY(100%);
  transition: .2s ease-in-out;
}

.latestupdates-card:hover .latestupdates-card__overlay {
  transform: translateY(0);
}

.latestupdates-card__header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2em;
  padding: 2em;
  border-radius: calc(var(--curve) * 1px) 0 0 0;    
  background-color: var(--surface-color);
  transform: translateY(-100%);
  transition: .2s ease-in-out;
}

.latestupdates-tag {
  background: #cccccc;
  border-radius: 50px;
  font-size: 12px;
  margin: 0;
  color: #fff;
  padding: 2px 10px;
  text-transform: uppercase;
  cursor: pointer;
}
.latestupdates-tag-teal {
  background-color: #47bcd4;
}
.latestupdates-tag-purple {
  background-color: #5e76bf;
}
.latestupdates-tag-pink {
  background-color: #cd5b9f;
}

.latestupdates-card__arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 100%;
  right: 0;      
  z-index: 1;
}

.latestupdates-card:hover .latestupdates-card__header {
  transform: translateY(0);
}

.latestupdates-card__thumb {
  flex-shrink: 0;
  width: 50px;
  height: 50px;      
  border-radius: 50%;      
}

.latestupdates-card__title {
  font-size: 16px;
  margin: 0 0 .3em;
  color: #203647;
}

.latestupdates-card__tagline {
  display: block;
  margin: 1em 0;
  font-family: "MockFlowFont";  
  font-size: .8em; 
  color: #D7BDCA;  
}

.latestupdates-card__status {
  font-size: .8em;
  color: #c8d8e4;
}

.latestupdates-card__description {
  padding: 0 2em 2em;
  margin: 0;
  color: #4DA8DA;
  font-family: "MockFlowFont";   
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}    
@media only screen and (max-width: 800px) {
  .latestupdates-body {
    width: 100%;
    
  }
}