
  
  .footer {
    background-color: #111;
    width: 88%;
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding: 30px;
    color: #fff;
    margin: auto;
    font-family: 'Sansita Swashed', cursive;
  }
  .fa{
    color: #fff;
  }
  .footer .footer-column:nth-child(1) {
    width: 25%;
  }
  .footer .footer-column:nth-child(2),
  .footer .footer-column:nth-child(3) {
    width: 30%;
  }
  
  .footer .footer-column:nth-child(4) {
    width: 10%;
  }
  
  .footer .footer-title {
    font-size: 25px;
    margin-bottom: 12px;
  }
  
  .footer .footer-column .footer-text {
    padding-right: 10px;
  }
  
  .footer .footer-link {
    list-style: none;
    
  }
  
  .footer .footer-link .footer-links a {
    text-decoration: none;
    line-height: 1.5;
    color:#fff
  }
  
  .footer .footer-link .footer-social-icons {
    display: inline-flex;
    padding: 40px 20px 0 0;
    font-size: 25px;
    
  }
  
  .footer .footer-column .footer-first-column .footer-flex,
  .footer .footer-column .footer-second-column .footer-flex {
    display: flex;
    padding: 10px;
  }
  
  
  .footer .footer-column .footer-image {
    width: 120px;
    height: 80px;
    margin-right: 15px;
    margin-bottom: 15px;
  }
  
  @media (max-width: 850px) {
    .footer {
    width: 100%;
      flex-direction: column;
      min-height: 100vh;
    }
    .footer .footer-column {
      width: 100% !important;
      margin-bottom: 25px;
    }
    .footer .footer-column:nth-child(4) {
      margin-bottom: 0;
    }
    .footer .footer-column .footer-text {
      padding-right: 0;
      text-align: justify;
    }
  }